  transform: perspective(1px) translateZ(0);
}

.v2-navbar.navbar .navigation-col .navbar-nav li a::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  transform: scaleX(0);
  transform-origin: 0 50%;
