  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.v2-navbar.navbar .navigation-col .navbar-nav li a:hover::before {
  transform: scaleX(1);
}

.v2-navbar.navbar .navigation-col .navbar-nav .active {
  position: relative;
}

.v2-navbar.navbar .navigation-col .navbar-nav .active a {
  background: #2CCDA0;
  border-bottom: none;
}

.v2-navbar.navbar .navigation-col .navbar-nav .active a::before {
  background: transparent;
}

.v2-navbar.navbar .navigation-col .navbar-nav .active::before {
  content: '';
  content: "";
  position: absolute;
  top: 0;
  left: 100%;
  right: 0;
  bottom: 0;
  background: #2CCDA0;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 8px;
}

.v2-navbar.navbar .navigation-col .bottom-side-nav li a {
  justify-content: center;
}

